import React, { PureComponent } from 'react';
import { connect } from 'react-redux'
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.css'
import { SwiperWrapped } from '../style'
import { actionCreators } from '../store'

class Swipe extends PureComponent {
  render() {
    const { focused, swipeList, mouseIn,mouseOut } = this.props;
    return (
      <SwiperWrapped className="swiper-container" onMouseEnter={mouseIn} onMouseLeave={mouseOut} >
        <div className="swiper-wrapper">
            {
              swipeList.map(item => {
                return <img className='banner-img swiper-slide' src={ item.get('src') } alt="" key={ item.get('id') }/>
              } )
            }
      </div>
        <div className="swiper-pagination"></div>
        <div className={focused ? "swiper-button-prev active" : "swiper-button-prev"}></div>
        <div className={focused ? "swiper-button-next active" : "swiper-button-next"}></div>
      </SwiperWrapped>
    )
  }

  componentDidUpdate(){
      if(!document.getElementsByClassName("swiper-pagination")[0].childNodes.length){
          this.initSwiper()
      }
  }

  initSwiper() {
    new Swiper('.swiper-container',{
      pagination:  '.swiper-pagination',
      navigation: {
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
    },
      loop: true,
      autoplay: 2000,
      autoplayDisableOnInteraction: false,
      speed: 1000
    });
  }

}



const mapStateToProps = (state) => ({
  focused: state.getIn(['home','swipeFocused']),
  swipeList: state.getIn(['home','swipe'])
});
const mapDispatchToProps = (dispatch) => ({
  mouseIn(){
    dispatch(actionCreators.swipeIn())
  },
  mouseOut(){
    dispatch(actionCreators.swipeOut())
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Swipe)


