import React, { PureComponent } from 'react';
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { LoginWrapper, LoginBox, Input, Button, Warring,
    AccountRemember, LoginProblem, SocialLogin
} from './style'
import { actionCreators } from './store'


class Login extends PureComponent {
  render() {
    const { loginStatus, login, warring } = this.props;
    if(!loginStatus){
        return (
            <LoginWrapper>
                <LoginBox>
                  <div className='loginTitle'></div>
                    <Input placeholder="" innerRef={(input) => {this.account = input}}/>
                    <Input placeholder="" type='password' innerRef={(input) => {this.password = input}}/>
                    <div className='loginBottom'>
                        <AccountRemember><input type="checkbox"/></AccountRemember>
                      <LoginProblem></LoginProblem>

                    </div>
                    {
                        warring ? <Warring>* </Warring> : null
                    }

                    <Button onClick={() => login(this.account, this.password)}></Button>
                    <SocialLogin>
                      <h6></h6>
                        <ul>
                            <li><i className="iconfont qq">&#xe630;</i></li>
                            <li><i className="iconfont weibo">&#xe641;</i></li>
                            <li><i className="iconfont wechat">&#xe62c;</i></li>
                            <li><i className="iconfont other">&#xe77f;</i></li>
                        </ul>
                    </SocialLogin>
                </LoginBox>
            </LoginWrapper>
        )
    }else {
      return <Redirect to='/' />
    }

  }
  componentDidMount() {

  }
}
const mapStateToProps = (state) => ({
  loginStatus: state.getIn(['login', 'login']),
    warring: state.getIn(['login', 'warring'])
});

const mapDispatchToProps = (dispatch) => ({
    login(accountEl, passwordEl){
      const account = accountEl.value;
      const password =  passwordEl.value;
      if( account === '' || password === '' ){
          dispatch(actionCreators.warring())
      }else{
          dispatch(actionCreators.login(account, password))
      }

    }
});

export default connect(mapStateToProps, mapDispatchToProps)(Login)


