import styled from 'styled-components'
import logoPic from '../../statics/logo.png'
export const HeaderWrapper = styled.div`
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 99;
    height: 56px;
    border-bottom: 1px solid #f0f0f0;
`;

export const HeaderContent = styled.div`
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
    height: 56px;
    border-bottom: 1px solid #f0f0f0;
`;

export const Logo = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 56px;
    width: 100px;
    background: url(${logoPic});
    background-size: contain;
`;

export const Nav = styled.div`
    width: 960px;
    height: 100%;
    padding-right: 70px;
    box-sizing: border-box;
    margin: 0 auto;
`;

export const NavItem = styled.div`
    line-height: 56px;
    padding: 0 15px;
    font-size: 17px;
    color: #333;
    &.left {
        float: left;
    }
    &.right {
        float: right;
        color: #969696;
    }
    &.active {
        color: #ea6f5a;
    }
    .iconfont {
      font-size: 20px;
    }
    
`;

export const SearchWrapper = styled.div`
    position: relative;
    float:left;
    .zoom {
        position: absolute;
        right: 5px;
        bottom: 5px;
        width: 30px;
        line-height: 30px;
        border-radius: 15px;
        text-align: center;
        &.focused{
            background: #777;
            color: #fff;
        }
    }   
`;

export const NavSearch = styled.input.attrs({
    placeholder: ''
})`
    width: 160px;
    height: 38px;
    margin-top: 9px;
    margin-left: 20px;
    padding: 0 35px 0 20px;
    box-sizing: border-box;
    border: none;
    outline: none;
    color: #666;
    border-radius: 19px;
    background: #eee;
    font-size: 14px;
    &::placeholder {
        color: #999;
    }
    &.focused {
        width: 240px;
    }
    //
    &.slide-enter {
        transition: all .2s ease-out;
        width:160px;
    }
    &.slide-enter-active{
        width: 240px;
    }
    &.slide-exit {
    transition: all .2s ease-out;
    width:240px;
    }
    &.slide-exit-active {
    width:160px;
    }
`;

export const SearchInfo = styled.div`
  z-index: 99;
  position: absolute;
  left: 20px;
  top: 56px;
  background: white;
  width: 240px;
  padding: 0 20px;
  box-shadow: 0 0 8px rgba(0, 0, 0, .2);
`;

export const SearchInfoTitle = styled.div`
  margin-top: 20px;
  margin-bottom: 15px;
  line-height: 20px;
  font-size: 14px;
  color: #969696;
`;

export const SearchInfoSwitch = styled.div`
  float: right;
  font-size: 13px;
  cursor: pointer;
  .spin {
    display: block;
    float: left;
    font-size: 12px;
    margin-right: 2px;
    transition: all .5s ease-in;
    transform-origin: center center;
  }
`;

export const SearchInfoItem = styled.a.attrs({
  href:'#'
})`
  display: block;
  float: left;
  line-height: 20px;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 0 5px;
  font-size: 12px;
  color: #787878;
  border:1px solid #969696;
  border-radius: 3px;
`;

export const SearchInfoList = styled.div`
  overflow: hidden; 
`;

export const Addition = styled.div`
    position: absolute;
    right: 0;
    top: 0;
    height: 56px;
`;

export const Button = styled.div`
    float: right;
    margin-top: 9px;
    margin-right: 20px;
    padding: 0 20px;
    line-height: 38px;
    border-radius: 19px;
    font-size: 14px;
    border: 1px solid #ec6149;
    &.reg {
        color: #ec6149;
    }
    &.writing {
        color: white;
        background-color: #ec6149;
    }
    `;


