import styled from 'styled-components'

export const HomeWrapper = styled.div`
  overflow: hidden;
  width: 960px;
  margin: 0 auto;
  padding-top: 56px;
`;

export const HomeLeft = styled.div`
  float: left;
  margin-left: 15px;
  padding-top: 30px;
  width: 625px;
  .banner-img {
    width: 625px;
    height: 270px;
  }
`;

export const HomeRight = styled.div`
  float: right;
  padding-top: 26px;
  width: 280px;
`;

export const BackToTop = styled.div`
  position: fixed;
  right: 100px;
  bottom: 100px;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border: 1px solid #ccc;
  font-size: 30px;
`;

export const TopicWrapper = styled.div`
  overflow: hidden;
  padding: 20px 0 10px 0;
  margin-left: -20px;
  border-bottom: 1px solid #dcdcdc;
`;

export const TopicItem = styled.div`
  float: left;
  background: #f7f7f7;
  box-sizing: border-box;
  height: 32px;
  line-height: 32px;
  padding-right: 10px;
  margin-left: 20px;
  margin-bottom: 15px;
  font-size: 14px;
  color: #000;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  cursor: pointer;
  .topic-img {
    float: left;
    display: block;
    margin-right: 10px;
    width: 32px;
    height: 32px;
  }
  &.more-topic {
    border: none;
    background: white;
    color: #787878;
  }
`;

export const ListItem = styled.div`
  overflow: hidden;
  padding: 20px 0;
  border-bottom: 1px solid #dcdcdc;
  .list-img {
    display: block;
    cursor: pointer;
    width: 125px;
    height: 100px;
    float: right;
    border-radius: 10px;
  }
`;

export const ListInfo = styled.div`
  width: 500px;
  float: left;
  .title {
    cursor: pointer;
    line-height: 27px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }
  .desc {
    line-height: 25px;
    font-size: 13px;
    color: #999;
  }
  .info {
    margin-top: 10px;
    color: #b4b4b4;
    font-size: 12px;
  }
  .info-detail {
    margin-right: 15px;
  }
  .iconfont {
    font-size: 10px;
    margin-right: 3px;
  }
`;

export const LoadMoreArticle =styled.div`
  widthL 100%;
  height: 40px;
  margin: 30px 0;
  line-height: 40px;
  background: #a5a5a5;
  text-align: center;
  border-radius: 20px;
  color: #fff;
  cursor: pointer;
`;

export const RecommendImg = styled.img`
  width: 280px;
  height: 50ox;
  margin-bottom: 4px;
  cursor: pointer;
`;
export const Download = styled.div`
  overflow: hidden;
  margin-top: 20px;
  padding: 10px 22px;
  width: 100%;
  border: 1px solid #f0f0f0;
  border-radius: 6px;
  background-color: #fff;
  cursor: pointer;
  img {
    width 60px;
    height: 60px;
    float: left;
  }
  .download-info {
    margin-top: 8px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 7px;
  }
  .download-title {
    font-size: 15px;
    line-height: 22px;
    color: #333;
  }
  .download-detail {
    margin-top: 4px;
    font-size: 13px;
    color: #999;
  }
`;
export const WriterTop = styled.div`
  margin-top: 20px;
  width: 100%
  color: #969696;
  font-size: 14px;
`;

export const WriterChange = styled.div`
  float: right;
  cursor: pointer;
  .spin {
    margin-right: 5px;
  }
`;

export const WriterItem = styled.div`
  overflow: hidden; 
  width: 100%;
  margin-top: 15px;
  .writer-img {
    float: left;
    cursor: pointer;
    width: 46px;
    height: 46px;
    border: 1px solid #ddd;
    border-radius: 50%;
  }
`;

export const WriterName = styled.div`
  margin-left: 60px;
  margin-top: 5px;
  font-size: 14px;
  line-height: 20px;
  span {
    cursor: pointer;
  }
`;

export const WriterFollow = styled.div`
  float: right;
  cursor: pointer;
  font-size: 13px;
  color: #42c02e;
`;

export const WriterInfo = styled.div`
  margin-left: 60px;
  margin-top: 2px;
  font-size: 12px;
  color: #969696;
  line-height: 20px;
`;

export const MoreWriter = styled.div`
  padding: 7px 7px 7px 12px;
  text-align: center;
  cursor: pointer;
  font-size: 13px;
  color: #787878;
  background-color: #f7f7f7;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
`;

export const SwiperWrapped = styled.div`
  .swiper-pagination-bullet {
    width: 25px;
    height: 2px;
    background-color: hsla(0,0%,47%,.4);
    display: inline-block;
    border-radius: 0;
    opacity: 1;
}
  .swiper-pagination-bullet-active {
    background-color: #fff;
  }
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
  .active {
    display: block;
  }
`;


