import { constants } from './index';
import { fromJS } from 'immutable';
import Axios from 'axios';

export const getHomeData = () => {
  return (dispatch) => {
      Axios.get('https://pdt3b9qwo.bkt.clouddn.com/home.json').then((res) => {
        const action = initHomeData(res.data.data);
        dispatch(action)
      });
  }
};

export const getHomeImgs = () => {
    return (dispatch) => {
        Axios.get('https://pdt3b9qwo.bkt.clouddn.com/homeImages.json').then((res) => {
            const action = initHomeImgs(res.data.data);
            dispatch(action)
        });
    }
};

export const getMoreArticle = (nextPage) => {
  return (dispatch) => {
    Axios.get('https://pdt3b9qwo.bkt.clouddn.com/homeList.json?page='+nextPage).then((res) => {
      const action = moreArticle(res.data.data, nextPage);
      dispatch(action)
    });
  }
};

export const swipeIn = () =>({
  type: constants.SWIPE_IN
});

export const swipeOut = () =>({
  type: constants.SWIPE_OUT
});

export const toggleTopShow = (isShow) => ({
  type: constants.TOGGLE_TOP_SHOW,
  isShow
});

const moreArticle = (data, nextPage) => ({
  type: constants.MORE_ARTICLE,
  data: fromJS(data),
  nextPage
});

const initHomeData = (data)=> ({
  type: constants.CHANGE_HOME_DATA,
  topicList: data.topicList,
  articleList: data.articleList,
  writerList: data.writerList,
  swipeList: data.swipeList
});

const initHomeImgs = (data)=> ({
    type: constants.INIT_HOME_IMGS,
    banner: data.banner,
    swipe: data.swiper,
});


