import styled from 'styled-components'

export const LoginWrapper = styled.div`
    position: absolute;
    top: 56px;
    left: 0;
    right: 0;
    bottom: 0;
    background: #eee;
`;

export const LoginBox = styled.div`
    position: relative;
    width: 400px;
    margin: 100px auto;
    padding-top: 80px;
    background: #fff;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    .loginTitle {
        position: absolute;
        top: 30px;
        right: 160px;
        width: 80px;
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 8px;
        border-bottom: 2px solid #ea6f5a;
        color: #ea6f5a;
        text-align: center;
    }
    .loginBottom {
        overflow: hidden;
        width: 200px;
        margin: 10px auto;
        color: #999;
    }
`;

export const Input = styled.input`
    display: block;
    width: 200px;
    height: 30px;
    line-height: 40px;
    padding: 0 10px;
    color: #777;
    margin: 10px auto;
    border: 1px solid #ccc;
    border-radius: 4px;
`;

export const Button= styled.div`
    width: 220px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    margin: 20px auto;
    background: #3194d0;
    border-radius: 15px;
    text-align: center;
    cursor: pointer;
`;

export const Warring = styled.div`
    position: relative;
    top: 5px;
    width: 220px;
    margin: 0 auto;
    color:  #e05244;
`;

export const AccountRemember = styled.div`
   float: left;
`;

export const LoginProblem = styled.div`
   float: right;
   font-size: 14px;
`;

export const SocialLogin = styled.div`
    width: 200px;
    margin: 30px auto;
    h6 {
        position: relative;
        margin: 0 0 10px;
        font-size: 12px;
        color: #b5b5b5;    
        text-align:center;
    }
    h6:before {
        content: "";
        border-top: 1px solid #b5b5b5;
        display: block;
        position: absolute;
        width: 60px;
        top: 5px;
        left: -10px;
    }
    h6:after {
        content: "";
        border-top: 1px solid #b5b5b5;
        display: block;
        position: absolute;
        width: 60px;
        top: 5px;
        right: -10px;
    }
    ul {
        padding-bottom: 30px;
        list-style: none;
        overflow: hidden;
    }
    li .iconfont {
        float: left;
        box-sizing: border-box;
        cursor: pointer;
        width:50px;
        text-align: center;
        font-size: 30px;
        color: #777;
    }
    .iconfont.qq{
        color: #498ad5;
    }
    .iconfont.wechat{
        color: #00bb29;
    }
    .iconfont.weibo{
        color: #e05244;
    }
`;


