import styled from 'styled-components'

export const DetailWrapper = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56px;
  padding-bottom: 100px;
  .wrapper {
  width: 620px;
  margin: 0 auto;
  }
`;

export const DetailHeader = styled.div`
  margin: 50px 0 20px 0;
  line-height: 44px;
  font-size: 34px;
  color: #333;
  font-weight: bold;
`;

export const DetailContent = styled.div`
  color: #2f2f2f;
  img {
    max-width: 100%;
  }
  p {
    margin: 25px 0;
    font-size: 16px;
    line-height: 30px;
  }
  bold {
    font-weight: bold;
  }
`;

export const DetailAd = styled.div`
  position: fixed;
  z-index: 99;
  right: 150px;
  top: 100px;
  width: 180px;
  height: 260px;
  cursor: pointer;
  background: url("https://cdn2.jianshu.io/assets/web/web-note-ad-side-banner-22096669b4c4b91c3b9266894e951aef.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
`;

export const DetailAuthor = styled.div`
  overflow: hidden;
  margin: 30px 0 40px;
  width: 100%;
  height:48px;
  
  img {
    float: left;
    width: 48px;
    height: 48px;
    border-radius: 50%;
  }
  
  .authorName {
    margin-top: 5px;
    margin-left: 56px;
    font-size: 16px;
    vertical-align: middle;
  }
  
  .authorFollow {
    position: relative;
    right: -5px;
    padding: 2px 8px;
    font-size: 12px;
    color: #fff;
    background-color: #42c02e;
    border-radius: 40px;
  }
  
  .authorInfo {
    margin-top: 10px;
    margin-left: 56px;
    font-size: 12px;
    color: #969696;
    span {
      padding-right: 5px;
    }
  } 
`;


