import { fromJS } from 'immutable';
import { constants } from './index'
//immutable
const defaultState = fromJS({
  login: false,
    warring: false
});

const reducer = (state = defaultState, action) => {
  switch(action.type) {
      case constants.CHANGE_LOGIN_STATUS:
        return state.merge({login: action.status,
                            warring: false
                            });
      case constants.WARRING_ALERT:
          return state.set('warring', true);
    default:
      return state
  }
};
export default reducer;




