import React, { PureComponent } from 'react';
import { connect } from 'react-redux';


import { HomeWrapper, HomeLeft, HomeRight, BackToTop } from './style'

import Topic from './components/Topic'
import List from './components/List'
import Recommend from './components/Recommend'
import Writer from './components/Writer'
import Swipe from './components/Swiper'
import { actionCreators } from './store'

class Home extends PureComponent {
  render() {
    return (
      <HomeWrapper>
        <HomeLeft>
          <Swipe></Swipe>
          <Topic></Topic>
          <List></List>
        </HomeLeft>
        <HomeRight>
          <Recommend></Recommend>
          <Writer></Writer>
        </HomeRight>
        {
          this.props.showBackTop ? <BackToTop onClick={this.handleScrollTop}>^</BackToTop> : null
        }

      </HomeWrapper>
    )
  }

  componentWillMount(){
    this.props.getHomeImgs();
  }

  componentDidMount() {
    this.props.changeHomeData();
    this.bindEvent();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.props.changeScrollTopShow)
}

  bindEvent() {
    window.addEventListener('scroll', this.props.changeScrollTopShow)
  }

  handleScrollTop(){
    window.scrollTo(0, 0)
  }

}

const mapStateToProps = (state) => ({
  showBackTop: state.getIn(['home', 'showBackTop'])
});

const mapDispatchToProps = (dispatch) => ({
  changeHomeData() {
    dispatch(actionCreators.getHomeData());
  },
  getHomeImgs() {
    dispatch(actionCreators.getHomeImgs())
   },
  changeScrollTopShow() {
    if(document.documentElement.scrollTop > 200){
      dispatch(actionCreators.toggleTopShow(true))
    }else {
      dispatch(actionCreators.toggleTopShow(false))
    }
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Home)


