import { constants } from './index';

import Axios from 'axios';

export const login = (account, password) => {
  return (dispatch) => {
     Axios.get('https://pdt3b9qwo.bkt.clouddn.com/login.json?account=' + account + '&password=' + password).then((res) =>{
         const result = res.data.login;
         if(result){
           const action = changeLogin(true);
           dispatch(action)
         }else {
           alert('')
         }
     })
  }
};

export const logout = () => ({
    type:constants.CHANGE_LOGIN_STATUS,
    status: false
});

export const warring = () => ({
    type:constants.WARRING_ALERT,
    status: false
});

const changeLogin = (status)=> ({
  type: constants.CHANGE_LOGIN_STATUS,
  status
});


