import { fromJS } from 'immutable';
import { constants } from './index'
//immutable
const defaultState = fromJS({
  topicList: [],
  articleList: [],
  writerList: [],
  swipeFocused: false,
  articlePage: 0,
  showBackTop: false,
  swipe: [],
  banner: []
});

const reducer = (state = defaultState, action) => {
  switch(action.type) {
    case constants.CHANGE_HOME_DATA:
      return state.merge({
        topicList: fromJS(action.topicList),
        articleList: fromJS(action.articleList),
        writerList: fromJS(action.writerList)
      });
      case constants.INIT_HOME_IMGS:
      return state.merge({
          banner: fromJS(action.banner),
          swipe: fromJS(action.swipe),
      });
    case constants.SWIPE_IN:
      return state.set('swipeFocused', true);
    case constants.SWIPE_OUT:
      return state.set('swipeFocused', false);
    case constants.MORE_ARTICLE:
      return state.merge({articleList: state.get('articleList').concat(action.data),
        articlePage: action.nextPage
      });
    case constants.TOGGLE_TOP_SHOW:
      return state.set('showBackTop', action.isShow);
    default:
      return state
  }
};
export default reducer;




