import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CSSTransition } from 'react-transition-group';
import { Link } from 'react-router-dom'

import {actionCreators as loginActionCreators} from '../../pages/login/store'
import { actionCreators } from './store';
import {
    HeaderWrapper, HeaderContent, Logo, Nav, NavItem,
    NavSearch, SearchWrapper, SearchInfo,SearchInfoTitle, SearchInfoSwitch, SearchInfoItem, SearchInfoList,
    Addition, Button
} from './style'

//,
class Header extends Component{
  render(){
    const { focused, list, handleInputFocus, handleInputBlur, login, logout } = this.props;
    return (
      <HeaderWrapper>
        <HeaderContent>
          <Link to='/'><Logo /></Link>
          <Nav>
            <Link to='/'><NavItem className='left active'></NavItem></Link>
            <NavItem className='left'>APP</NavItem>
              {
                login ?
                    <NavItem className='right' onClick={logout}></NavItem>
                    :
                    <Link to='/login'>
                        <NavItem className='right'></NavItem>
                    </Link>
              }

            <NavItem className='right'><i className="iconfont">&#xe636;</i></NavItem>
            <SearchWrapper>
              <CSSTransition
                in={focused}
                timeout={200}
                classNames='slide'
              >
                <NavSearch
                  className={focused ? 'focused' : ''}
                  onFocus={() => {handleInputFocus(list)} }
                  onBlur={handleInputBlur}
                />
              </CSSTransition>
              <i className={focused ? 'focused iconfont zoom' : 'iconfont zoom'}>&#xe6dd;</i>
              { this.getSearchInfo() }
            </SearchWrapper>
            <Addition>
              <Button className='writing'><i className="iconfont">&#xe60e;</i></Button>
              <Button className='reg'></Button>
            </Addition>
          </Nav>
        </HeaderContent>
      </HeaderWrapper>
    )
  }
  getSearchInfo(){
    const { focused, mouseIn, list, page, totalPage, changePage, handleMouseEnter, handleMouseLeave } = this.props;
    //immutableJS
    // const jsList = list.toJS();
    const currentList = [];
    // eslint-disable-next-line
    list.map((item,index )=>{
      if((index >= (page - 1) * 10) && (index < page * 10)){
        currentList.push(<SearchInfoItem key={item}>{item}</SearchInfoItem>)
      }
    });
    // for (let i=(page - 1) * 10; i < page * 10; i++){
    //   currentList.push(<SearchInfoItem key={jsList[i]}>{jsList[i]}</SearchInfoItem>)
    // }
    if(focused || mouseIn){
      return (
        <SearchInfo
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <SearchInfoTitle>
            
            <SearchInfoSwitch onClick={() => changePage(page, totalPage, this.spin)}>
              <i ref={(icon) => {this.spin = icon}} className="iconfont spin">&#xe7ea;</i>
              
            </SearchInfoSwitch>
          </SearchInfoTitle>
          <SearchInfoList>
            {currentList}
          </SearchInfoList>
        </SearchInfo>
      )
    }else {
      return
    }
  }
}

//,
// const Header= (props) => {
//      return ()
// };

const mapStateToProps = (state) => {
  return {
    //immutable
    // focused:state.get('header').get('focused')
    focused: state.getIn(['header', 'focused']),
    list: state.getIn(['header', 'list']),
    page: state.getIn(['header', 'page']),
    totalPage: state.getIn(['header', 'totalPage']),
    mouseIn: state.getIn(['header', 'mouseIn']),
    login: state.getIn(['login', 'login'])
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    handleInputFocus(list){
      if(list.size === 0){
        dispatch(actionCreators.getList());
      }
      dispatch(actionCreators.inputFocus())
    },
    handleInputBlur(){
      dispatch(actionCreators.inputBlur())
    },
    changePage(page, totalPage, spin){
      let originAngle = spin.style.transform.replace(/[^0-9]/g,'');
      if(originAngle){
        originAngle = parseInt(originAngle, 10);
      }else{
        originAngle = 0;
      }
      spin.style.transform = 'rotate(' + (originAngle + 180) + 'deg)';
      if(page < totalPage){
        dispatch(actionCreators.changePage(page + 1));
      }else {
        dispatch(actionCreators.changePage(1))
      }
    },
    handleMouseEnter(){
      dispatch(actionCreators.mouseEnter())
    },
    handleMouseLeave(){
      dispatch(actionCreators.mouseLeave())
  },
    logout(){
      dispatch(loginActionCreators.logout())
    }
  }
};
export default connect(mapStateToProps, mapDispatchToProps)(Header)


