import { constants } from './index';
import { fromJS } from 'immutable';
import Axios from 'axios';

export const getArticle = (id) => {
  return (dispatch) => {
      Axios.get('\n' +
          'http://pdt3b9qwo.bkt.clouddn.com/detail.json?id='+id).then((res) => {
        const action = initArticleDetails(res.data.data.title, res.data.data.content);
        dispatch(action)
      });
  }
};

// export const swipeIn = () =>({
//   type: constants.SWIPE_IN
// });

const initArticleDetails = (title, content) => ({
  type: constants.ARTICLE_DETAIL,
  title: fromJS(title),
  content: fromJS(content)
});




