import React, { Component } from 'react';
import { connect } from 'react-redux'
import { DetailWrapper, DetailHeader, DetailAd,
  DetailContent, DetailAuthor } from '../detail/style'


import { actionCreators } from '../detail/store/index'

class Detail extends Component {
  render() {
    const { title, content } = this.props;
    return (
      <DetailWrapper>
      <div className='wrapper'>
        <DetailHeader>{ title }</DetailHeader>
        <DetailAuthor>
          <img src="https://upload.jianshu.io/users/upload_avatars/5599696/05d94b2f-e491-4d48-9e4c-d85a1e5daa7b.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/96/h/96" alt=""/>
          <div className='authorName'>_baoxicong<span className='authorFollow'>+</span></div>
          <div className='authorInfo'>
            <span>2018.07.09 14:19*</span>
            <span> 64</span>
            <span> 949</span>
            <span> 20</span>
            <span> 24</span>
          </div>
        </DetailAuthor>
        <DetailContent dangerouslySetInnerHTML={{__html: content}} />
        <DetailAd>
        </DetailAd>
        </div>
      </DetailWrapper>
    )
  }
  componentDidMount() {
    this.props.getDetail(this.props.match.params.id);
  }
}

const mapStateToProps = (state) => ({
  title: state.getIn(['detail', 'title']),
  content: state.getIn(['detail', 'content'])
});

const mapDispatchToProps = (dispatch) => ({
  getDetail(id){
    dispatch(actionCreators.getArticle(id));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Detail)


