import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';

import { ListItem, ListInfo, LoadMoreArticle } from '../style';

import { actionCreators } from '../store'


class List extends PureComponent {
  render() {
    const { list, page, getMoreArticle } = this.props;
    return (
      <div>
          {
            list.map((item,index) => {
              return (
                  <Link key={page+'-'+index} to={'/detail/' + item.get('id')}>
                  <ListItem >
                    <img className='list-img' src={item.get('img')} alt=""/>
                    <ListInfo>
                      <h3 className='title'>{item.get('title')}</h3>
                      <p className='desc'>{item.get('desc')}</p>
                      <p className='info'>
                        <span className='info-detail'>{item.get('author')}</span>
                        <span className='info-detail'><i className="iconfont">&#xe770;</i>{item.get('comments')}</span>
                        <span className='info-detail'><i className="iconfont">&#xe621;</i>{item.get('like')}</span>
                      </p>
                    </ListInfo>
                  </ListItem>
                  </Link>
              )
            })
          }
        <LoadMoreArticle onClick={() => getMoreArticle(page)}></LoadMoreArticle>
      </div>

    )
  }

}

const mapStateToProps = (state) => ({
  list: state.getIn(['home', 'articleList']),
  page: state.getIn(['home', 'articlePage'])
});

const mapDispatchToProps = (dispatch) => ({
  getMoreArticle(page){
    dispatch(actionCreators.getMoreArticle(page+1))
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(List);


