import { fromJS } from 'immutable';
import { constants } from './index'
//immutable
const defaultState = fromJS({
  title: "",
  content: ""
});

const reducer = (state = defaultState, action) => {
  switch(action.type) {
    case constants.ARTICLE_DETAIL:
      return state.merge({
        title: action.title,
        content: action.content
      });
    default:
      return state
  }
};
export default reducer;




